<template>
  <main class="content container" v-if="order">
    <div class="content__top">
      <BaseBreadcrumbs :items="linkItems" />

      <h1 class="content__title">
          <span> {{order.id}}</span>
      </h1>
    </div>

    <section class="cart">
      <div class="cart__form form">
        <div class="cart__field">
          <p class="cart__message">
             &nbsp;  .
            &nbsp;    &nbsp; .
               &nbsp; &nbsp;     .
          </p>

          <ul class="dictionary">
            <OrderInfoDictionary
              title=""
              :value="order.name"
            />

            <OrderInfoDictionary
              title=" "
              :value="order.address"
            />

            <OrderInfoDictionary
              title=""
              :value="order.phone"
            />

            <OrderInfoDictionary
              title="Email"
              :value="order.email"
            />

            <OrderInfoDictionary
              title=" "
              :value="order.paymentType"
            />
          </ul>
        </div>

        <OrderCartBlock
          :items="order.basket && order.basket.items"
          :totalQuantity="totalQuantity"
          :totalPrice="order.totalPrice"
          :deliveryPrice="+order.deliveryType.price"
        />
      </div>
    </section>
  </main>
</template>

<script>
import { mapGetters } from 'vuex';
import BaseBreadcrumbs from '@/components/BaseBreadcrumbs.vue';
import OrderInfoDictionary from '@/components/OrderInfoDictionary.vue';
import OrderCartBlock from '@/components/OrderCartBlock.vue';

export default {
  name: 'OrderInfoPage',
  data() {
    return {
      linkItems: [{ title: ' ' }],
    };
  },
  components: { OrderInfoDictionary, OrderCartBlock, BaseBreadcrumbs },
  computed: {
    ...mapGetters({ order: 'orderInfo', totalQuantity: 'orderTotalQuantity' }),
  },
  created() {
    if (this.$store.orderInfo && this.$store.orderInfo.id === this.$route.params.id) {
      return;
    }

    this.$store.dispatch('loadOrderInfo', this.$route.params.id);
  },
};
</script>
